const store = createStore(reducer, applyMiddleware(thunk));
class Download extends Component {
    render() {
        return (
          <div className="download_popap popap">
            <h2><strong>7 days </strong> free</h2>
            <button>Download APP NOW</button>
          </div>
        )
    }
}
class Possibility extends Component {
    render() {
        return (
          <div className="possibility_popap resizable popap">
            <div className="resizer top" id="swipe-wrap"></div>
            <div className="possibility_popap_content">
                <section>
                    <h1><strong>Possibility </strong> App</h1>
                    <p>about this Possibility</p>

                    <figure className="phone_figure left">
                        <img src="./images/possibility/1_Home.png" alt="1_Home.png" />
                        <figcaption>1_Home</figcaption>
                    </figure>

                    <article>
                        <h2>1_Home.png</h2>
                        <p>
                            Videoder currently lets you download videos from sites like youtube, instagram, facebook, hotstar, voot, vk and new ones are being added every week as per our users request.
                        </p>
                    </article>

                    <figure className="phone_figure right">
                        <img src="./images/possibility/3_Home.png" alt="3_Home.png" />
                        <figcaption>3_Home</figcaption>
                    </figure>

                    <article>
                        <h2>Batch Downloading.</h2>
                        <p>
                            Make a collection of songs and videos while exploring and set them to download in just one go. This is the power of multiple downloading feature. Download hundreds of songs with just a click.
                        </p>
                    </article>

                    <figure className="phone_figure left">
                        <img src="./images/possibility/5_Video_QualityList.png" alt="5_Video_QualityList.png" />
                        <figcaption>5_Video_QualityList.png</figcaption>
                    </figure> 

                    <article>
                        <h2>Upto 10X Faster Downloading.</h2>
                        <p>
                            Videoder uses multiple network connections to accelerate the downloading speed. You can set the number of connections according to your network quality to gain maximum speed.
                        </p>
                    </article>

                    <figure className="phone_figure right">
                        <img src="./images/possibility/8_Settings.png" alt="8_Settings.png" />
                        <figcaption>8_Settings.png</figcaption>
                    </figure>

                </section>

            </div>
          </div>
        )
    }
  }
