document.addEventListener('DOMContentLoaded', function(){
    const body=document.querySelector('body');
    function start(){
      body.classList.add('start');

    }
  
        let e=document.querySelector(".image_z");
        let activeRotate=true;
        function clicks(ev){
            if(activeRotate){
                activeRotate=false;
                return false;
            }
            activeRotate=true;
            let i=(ev)?"right":"left";
            body.classList.remove("rotate_right","rotate_left");
            setTimeout(() => body.classList.add("rotate_"+i), 10);
            setTimeout(() => activeRotate=false, 500);
        }
  
  
        let menu=document.getElementById("menu_icon");
        let main=document.querySelector(".main");
        

        // let path_a="M 7,5 C 5.7894785,5 4.7862649,6.0119972 4.7862649,7.2687576 v 2.8351874 c 0,1.26423 26.2182871,27.404787 27.4288091,27.404787 h 2.766427 c 1.226282,0 2.213735,-1.004528 2.213735,-2.268758 V 32.404787 C 37.195236,31.148027 10.992709,5 9.7664274,5 Z";
            
        //   let path_b="m 33.027574,5.5018382 c -1.186123,0 -27.7552531,26.7810768 -27.7552531,27.8377258 v 2.388428 c 0,1.058725 0.958836,1.911255 2.144959,1.911255 h 2.6826351 c 1.188714,0 27.756211,-26.781077 27.756211,-27.8377258 V 7.413093 c 0,-1.0587245 -0.957203,-1.9112548 -2.145917,-1.9112548 Z";
        //   let path_c=path_b;
        //   let path_d="m 24.28125,20.720355 c 0.535204,0.27518 0.535204,0.969539 0,1.244719 l -3.572507,1.836881 c -0.531087,0.273081 -1.191893,-0.072 -1.191893,-0.622359 v -3.673763 c 0,-0.550392 0.660806,-0.895441 1.191893,-0.622359 z";
          
            function menuClick() {

                if(menu.classList.contains('menu_active')){
                    menu.querySelectorAll('.button-play').forEach((e, i) => {
                        e.beginElement();
                    });
                    menu.classList.remove('menu_active');
                }else{
                    menu.querySelectorAll('.button-pause').forEach((e, i) => {
                        e.beginElement();
                    });
                    menu.classList.add('menu_active');
                }
                return false;
            };
            //menu.addEventListener("click", menuClick);

            

        function menuBar(){
            //menu.classList.toggle("menu_active");
            menuClick();
            main.classList.toggle("menu_active");
        }
        menu.addEventListener("click", menuBar);
  
        let paginator = document.querySelector(".paginator"),
        paginators = paginator.querySelectorAll("div"),
        paginatorBg = paginator.querySelector("i"),
        activeTab=0,
        tabs=paginators.length-1;
        
        let paginatorWith=(paginator.offsetWidth/4);
        paginatorBg.style.width=paginatorWith+'px';
        function paginatorChange(e,el){
            console.log("activeTab: "+activeTab+" el: "+el);
            clicks(activeTab<=el);
            if(el<0)el=tabs;
            if(el>tabs)el=0;
            body.classList.remove('tab_'+activeTab);
            setTimeout(() => body.classList.add("tab_"+el), 30);
            paginators[activeTab].classList.remove("active");
            activeTab=el;
            paginators[el].classList.add("active");
            paginatorBg.style.width=(paginatorWith*(1+el))+'px';
        }
        paginators.forEach((e, i) => {
            e.addEventListener("click", (e)=>{if(i!==activeTab)paginatorChange(e,i)})
        });
  
        document.onkeydown = checkKey;
        function checkKey(e) {
            e = e || window.event;
            switch(e.keyCode) {
                case 38: 
                    // up arrow
                    break
            
                case 40:
                    // down arrow
                    break
        
                case 37:
                    paginatorChange(e,activeTab-1);
                    break
        
                case 39:
                    paginatorChange(e,activeTab+1);
                    break
                    
                default:
                    //another
                    break
              }
  
            // if (e.keyCode =='38') {
            //     // up arrow
            // }
            // else if (e.keyCode == '40') {
            //     // down arrow
            // }
            // else if (e.keyCode =='37') {
            // // left arrow
            //     paginatorChange(e,activeTab-1);
            // }
            // else if (e.keyCode =='39') {
            // // right arrow
            //     paginatorChange(e,activeTab+1);
            // }
        }


	var xDown = null;
	var yDown = null;

	function handleTouchStart(evt) {
		xDown = evt.touches[0].clientX;
		yDown = evt.touches[0].clientY;
	}

	function handleTouchMove(evt) {
		if (!xDown || !yDown) {
			return;
		}

		var xUp = evt.touches[0].clientX;
		var yUp = evt.touches[0].clientY;

		var xDiff = xDown - xUp;
		var yDiff = yDown - yUp;
        //console.log("xDiff: "+xDiff+"   Math.abs(xDiff): "+Math.abs(xDiff)+" > Math.abs(yDiff): "+Math.abs(yDiff));
		if (Math.abs(xDiff) > Math.abs(yDiff)) {
			if (xDiff > 0) {
				//swipe(1);
                paginatorChange(e,activeTab+1);
			} else {
				//swipe(-1);
                paginatorChange(e,activeTab-1);
			}
		}
		/* reset values */
		xDown = null;
		yDown = null;
	}

	main.addEventListener('touchstart', handleTouchStart, false);
	main.addEventListener('touchmove', handleTouchMove, false);
    paginatorChange(e,activeTab);









    function makeResizableDiv(div) {
        const element = document.querySelector(div);
        const resizers = document.querySelectorAll(div + ' .resizer');

        const minimum_size = 200;
        let original_width = 0;
        let original_height = 0;
        let original_x = 0;
        let original_y = 0;
        let original_mouse_x = 0;
        let original_mouse_y = 0;

        for (let i = 0;i < resizers.length; i++) {
        const currentResizer = resizers[i];
        currentResizer.addEventListener('mousedown', function(e) {
            e.preventDefault()
            original_width = parseFloat(getComputedStyle(element, null).getPropertyValue('width').replace('px', ''));
            original_height = parseFloat(getComputedStyle(element, null).getPropertyValue('height').replace('px', ''));
            original_x = element.getBoundingClientRect().left;
            original_y = element.getBoundingClientRect().top;
            original_mouse_x = e.pageX;
            original_mouse_y = e.pageY;
            window.addEventListener('mousemove', resize)
            window.addEventListener('mouseup', stopResize)
        })
        
        function resize(e) {
            element.style.transition='none';

            if (currentResizer.classList.contains('top')) {
                //element.style.height = e.pageY + element.getBoundingClientRect().top + 'px'

                const height = original_height - (e.pageY - original_mouse_y)
                if (height > minimum_size) {
                    element.style.height = height + 'px'
                    
                    //element.style.top = original_y + (e.pageY - original_mouse_y) + 'px'
                }
            }
        }
        
        function stopResize() {
            element.style.transition='';
            window.removeEventListener('mousemove', resize)
        }
        }
    }
    


    if(e.type == 'touchstart' || e.type == 'touchmove' || e.type == 'touchend' || e.type == 'touchcancel'){
        var touch = e.originalEvent.touches[0] || e.originalEvent.changedTouches[0];
        var x = touch.pageX;
        var y = touch.pageY;
    } else if (e.type == 'mousedown' || e.type == 'mouseup' || e.type == 'mousemove' || e.type == 'mouseover'|| e.type=='mouseout' || e.type=='mouseenter' || e.type=='mouseleave') {
        var x = e.clientX;
        var y = e.clientY;
    }



    var touchstart_x,touchstart_y,touchmove_x,touchmove_y;
    document.getElementById("swipe-wrap").addEventListener("touchstart",
        function clicked(e) {
            var br = document.getElementById("swipe-wrap").getBoundingClientRect();
            var evt = (typeof e.originalEvent === 'undefined') ? e : e.originalEvent;
            var touch = evt.touches[0] || evt.changedTouches[0];
            touchstart_x = touch.clientX - br.left;
            touchstart_y = touch.clientY - br.top;
            console.log("touchstart x: " + touchstart_x + " y: " + touchstart_y);
        }
    );    

    document.getElementById("swipe-wrap").addEventListener("touchmove",
        function clicked(e) {
            e.preventDefault();
            var br = document.getElementById("swipe-wrap").getBoundingClientRect();
            var evt = (typeof e.originalEvent === 'undefined') ? e : e.originalEvent;
            var touch = evt.touches[0] || evt.changedTouches[0];
            touchmove_x = touch.clientX - br.left;
            touchmove_y = touch.clientY - br.top;
            let element = document.querySelector('.resizable');
            let original_height = parseFloat(getComputedStyle(element, null).getPropertyValue('height').replace('px', ''));
            let height = original_height - (touchmove_y - touchstart_y);
            element.style.transition='none';
            element.style.height = height + 'px';
            //console.log("touchmove x: " + touchmove_x + " y: " + touchmove_y);
        }
    );
    document.getElementById("swipe-wrap").addEventListener("touchend", ()=>{
        let element = document.querySelector('.resizable');
        element.style.transition='';
        console.log("touchend x: " + touchmove_x + " y: " + touchmove_y);
    });
    document.getElementById("swipe-wrap").addEventListener("touchcancel", ()=>{
        let element = document.querySelector('.resizable');
        element.style.transition='';
        console.log("touchcancel x: " + touchmove_x + " y: " + touchmove_y);
    });

    makeResizableDiv('.resizable')




    start();









});

(function init100vh(){
    function setHeight() {
        var vh = window.innerHeight * 0.01;
        document.documentElement.style.setProperty('--vh', `${vh}px`);
    }
    setHeight();
    window.addEventListener('resize', setHeight);
})();



